.videos {
    padding: 0 0 27px;
    background-color: #F3F3F3;
}

.videos-slider {
    width: 100%;
    max-width: 970px;
    margin: 0 auto;
    padding: 0 80px;
    .owl-nav {
        width: 100%;
        left: 0;
    }
}


.videos-item {
    width: 100%;
    max-width: 300px;
    .videos-preview {
        position: relative;
        display: block;
        width: 100%;
        max-height: 187px;
        border-radius: 4px 4px 0 0;
        overflow: hidden;
        & img {width: 100%;}
        & .videos-preview-layout {
          position: absolute;
          top: 0;
          left: 0;
          display: flex;
          align-items: center;
          justify-content: center;
          opacity: 0;
          width: 100%;
          height: 100%;
          background-color: rgba(0, 0, 0, 0.4);
          transition: 0.3s ease;
          & svg {
            width: 40px;
            height: 40px;
            fill: #ffffff;
            transition: 0.3s ease;
          }
          &:hover {
            & svg {transform: scale(1.05);}
          }
        }
        &:hover {
          & .videos-preview-layout {opacity: 1;}
        }
    }
    .videos-cnt {
        display: block;
        width: 100%;
        background-color: #FFFFFF;
        padding: 16px 12px 10px 8px;
        transition: 0.3s ease;
        & span {
            display: -webkit-box;
            -webkit-line-clamp: 2;
            overflow: hidden;
            -webkit-box-orient: vertical;
            height: 40px;
            font-family: 'HelveticaNeueCyr-Roman';
            font-size: 16px;
            line-height: 20px;
            color: rgba(0, 0, 0, 0.9);
            &:hover {color: #00877C;}
        }
    }
}

